import React, {useState} from 'react';

import {withRouter} from "react-router-dom";

import './header.styles.scss';

const Header = ({history, match}) => {

    // const [isMenuHidden, setIsMenuHidden] = useState(false);

    const handleMenuToggle = () => {
      document.querySelector('.sidebar-container').classList.toggle('sidebar-container-hide');
      document.querySelector('.sidebar').classList.toggle('sidebar-hide');
    };

  return(
      <header className="header">

          <div className="header__brand">

              <svg className='menu' onClick={handleMenuToggle} version="1.1" xmlns="http://www.w3.org/2000/svg" width="44" height="32" viewBox="0 0 44 32">
                <title>menu3</title>
                <path d="M0 6h28v6h-28v-6zM0 14h28v6h-28v-6zM0 22h28v6h-28v-6z"></path>
                <path d="M31 14l6 6 6-6z"></path>
              </svg>

              <span onClick={() => history.push(`${match.url}`)}>Computer On Live</span>
          </div>

          <form action="#" className="search">
              <input type="text" className="search__input" placeholder="Search products"/>
              <button className="search__button">

                  <svg className="search__icon" version="1.1" xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 20 20">
                    <title>magnifying-glass</title>
                    <path d="M17.545 15.467l-3.779-3.779c0.57-0.935 0.898-2.035 0.898-3.21 0-3.417-2.961-6.377-6.378-6.377s-6.186 2.769-6.186 6.186c0 3.416 2.961 6.377 6.377 6.377 1.137 0 2.2-0.309 3.115-0.844l3.799 3.801c0.372 0.371 0.975 0.371 1.346 0l0.943-0.943c0.371-0.371 0.236-0.84-0.135-1.211zM4.004 8.287c0-2.366 1.917-4.283 4.282-4.283s4.474 2.107 4.474 4.474c0 2.365-1.918 4.283-4.283 4.283s-4.473-2.109-4.473-4.474z"></path>
                  </svg>

              </button>
          </form>

            <a href='#contact-us' className="header__icon-box">

                <svg version="1.1" xmlns="http://www.w3.org/2000/svg" width="32" height="32" viewBox="0 0 32 32">
                <title>envelop</title>
                <path d="M29 4h-26c-1.65 0-3 1.35-3 3v20c0 1.65 1.35 3 3 3h26c1.65 0 3-1.35 3-3v-20c0-1.65-1.35-3-3-3zM12.461 17.199l-8.461 6.59v-15.676l8.461 9.086zM5.512 8h20.976l-10.488 7.875-10.488-7.875zM12.79 17.553l3.21 3.447 3.21-3.447 6.58 8.447h-19.579l6.58-8.447zM19.539 17.199l8.461-9.086v15.676l-8.461-6.59z"></path>
              </svg>

              <span>Contact</span>
            </a>
      </header>
  );
};

export default withRouter(Header);